.nav-btn {
  padding: 15px;
  margin-left: 5px;
  margin-top: 2px;
  display: inline-block;
  border-radius: 50px;
  background-color: gray;
  text-decoration: none;
  color: white;
}

.nav-btn:hover {
  color: black;
  background-color: lightblue;
}

.Samay {
  width: 100%;
  height: 40px;
  margin-left: auto;
  margin-right: 0;
  text-align: center;
}

.Question_pallete {
  width: 25%;
  position: fixed;
  right: 0;
  border-style: solid;
  float: right;
}

.qtn-part {
  display: grid;
  grid-template-columns: 3fr 1fr;
  border-style: solid;
  width: 70%;
  margin-bottom: 20%;
}

.btn {
  color: white;
  margin-left: 20px;
  background-color: rgb(38, 38, 204);
  padding: 15px;
  border-radius: 50px;
}

.btn-area {
  width: 70%;
  height: 10%;
  border-style: solid;
  background-color: gray;
  position: fixed;
  bottom: 0;
  left: 0;
}

.qtn {
  border-style: solid;
  width: 130%;
}

.qtn-img {
  width: 100%;
}

.Center_karo {
  padding-top: 15%;
  padding-left: 20%;
  padding-right: 20%;
}

.Name_area {
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  border-style: solid;
}
